import i18n from "i18next";
import { initReactI18next } from "react-i18next";

// Import JSON translation files
import enUS from "./locales/en-US.json";

import AppStorage, { DIR, LANG, TRANS } from "@/utils/services/app.storage";
import { SessionLang } from "@/utils/services/app.event";

export const languages = [
    {
        code: "en-US",
        name: "English",
        locale: "English",
        dir: "ltr",
    },
] as const;

export const resources = {
    "en-US": {
        translation: enUS,
    },
} as const;

// Initialize with stored language if available
const storedLang = AppStorage.getData(LANG) || "en-US";

i18n.use(initReactI18next).init({
    resources,
    lng: storedLang,
    fallbackLng: "en-US",
    interpolation: {
        escapeValue: false,
    },
    react: {
        useSuspense: false,
    },
});

// Language change listener
i18n.on("languageChanged", (lng: string) => {
    try {
        const dir = languages.find(l => l.code === lng)?.dir || "ltr";

        AppStorage.setData(DIR, dir);
        AppStorage.setData(LANG, lng);
        SessionLang.value = lng;
        if (lng in resources) {
            AppStorage.setData(TRANS, (resources as any)[lng].translation);
        }
    } catch (error) {
        console.error("Language change error", error);
    }
});

export default i18n;
